	import {BLACKADD} from '../../../utils/api.js'
	import WxValidate from '../../../utils/WxValidate.js'
	export default {
		data() {
			return {
				name: "",
				mobile: "",
				addShow: "",
				isdisable: false,
				remark: '',
				address: ''
				

			}
		},
		onLoad() {
			this.initValidate();
		},
		methods: {
			// 规则校验
			initValidate() {
				// 规则
				const rules = {
					mobile: {
						required: true,
						tel:true
					}
				}
				// 提示
				const messages = {
					mobile: {
						required: "请输入手机号"
					}
				}
				this.WxValidate = new WxValidate(rules, messages)
			},
			// 添加联系人列表
			addContact(e) {
				this.isdisable = true
				var _this =this
				setTimeout(function(){
					_this.isdisable = false
					_this.$forceUpdate()
				},1000)
				const params = e.detail.value
					if (!this.WxValidate.checkForm(params)) {
						const error = this.WxValidate.errorList[0]
						// 可以自定义提示
						uni.showToast({
							title: `${error.msg}`,
							icon: 'none'
						});
						return false
					} else {	
						console.log(params)
						_this.$request.url_request(BLACKADD,params,'GET',res=>{
							uni.showToast({
								title:'黑名单添加成功！',
								icon:'none',
								duration:1000
							})
							setTimeout(()=>{
								uni.navigateTo({
									url:'../RejectionBlacklist/RejectionBlacklist'
								})
							},1000)
						},err=>{})
					}
				}
	}
 }
